@import '../../mixins/mixin.scss';

.curtain{
    @include flex(center,center);
    z-index: 1;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    animation: upCurtain;
    animation-duration: 1.5s;
    animation-delay: 4s;
}

.text_curtain {
    font-size: 1.7em;
    font-weight: 900;
  }
  .text_curtain > * {
    color: #fff;
  }
  .text_curtain span {
    display: inline-flex;
  }
  .text_curtain span:nth-child(2) {
    letter-spacing: -1em;
    overflow: hidden;
    animation: reveal 4000ms ;
    animation-delay: 1s;
  }
  @keyframes reveal {
    0% {
      opacity: 1;
      letter-spacing:-1em;
    }

    50% {
      opacity: 1;
      letter-spacing: 0.7em;
    }


    100%{
      letter-spacing: -1em;

    }

  }

  @keyframes upCurtain {
    0%{
      height: 100%;
    }

    100%{
      height: 0;
    }

  }

  @media (max-width: 780px){
    .text_curtain{
      font-size: 1.2em;
    }
  }

  @media (max-width: 500px){
    .text_curtain{
      font-size: 0.9em;
    }
  }
  